@import "main";

// g
* {
    box-sizing: border-box;
}

// scss color var
$color-black: #000000; //black
$color-white: #ffffff; //white

// fields
$color-glade-green: #667D65;
$color-pampas: #F3F2EA;
$color-green-white: #F2F5EF;
$color-green-smoke: #A5A463;
$color-flax-smoke: #807F61;
$color-white-rock: #EAE8D6;

// interaction pop
$color-chartreuse: #DBFF00;

// complement
$color-dust-storm: #E5CFD0;
$color-muted-pink: #C79B9A;
$color-sepia: #A15246;

// blacks
$color-heavy-metal: #232823;
$color-holly: #000503;

// aux
$color-kangaroo: #CBCABE;
$color-eagle: #C0BFA7;

// scss space var
$view-width: 80vw;
$medium-entry: 72em;
$large-entry: 108em;
$wrapper-padding: 9.375em;

//style mixins
@mixin VR_left {
    padding-left: 25px;
    border-left: 1px solid $color-black;  
}

// type
h1 {
    font-family: "freight-big-pro", sans-serif;
    font-weight: 400;
    color: $color-black;
    font-size: 7.25rem;
    line-height: 0.8em;
    text-transform: uppercase;
}

h2 {
    font-family:"neue-haas-grotesk-display";
    font-weight: 600;
    color: $color-black;
    font-size: 3.5rem;
}

h2 {
    font-family: "aw-conqueror-didot";
    font-style: normal;
    font-weight: 400;
    color: $color-black;
    font-size: 3.5rem;
    line-height: 89%;
    margin: 0;
}

h3 {
    font-family: "freight-big-pro", serif;
    font-weight: 400;
    color: $color-black;
    font-size: 2.25rem;
}

h4 {
    font-family: "freight-big-pro", serif;
    font-weight: 400;
    color: $color-black;
    font-size: 1.25rem;
    font-style: italic;
}

h5 {
    font-family: "neue-haas-grotesk-display", sans-serif;
    font-weight: 600;
    color: $color-black;
    font-size: 0.75rem;
    text-transform: uppercase;
}

@mixin p-sans {
    font-family:"aktiv-grotesk";
    font-weight: 400;
    color: $color-black;
    font-size: 1rem;
    line-height: 1.5rem;
    widows: 0;
}

@mixin p-serif {
    font-family: "freight-big-pro", serif;
    font-weight: 400;
    color: $color-black;
    font-size: 1.125rem;
}

p {
    @include p-sans();
}

a {
    font-family:"aktiv-grotesk-extended";
    font-weight: 700;
    color: $color-black;
    font-size: 1rem;
    text-decoration: underline;

    // &:hover {
    //     text-decoration: none;
    //     background-color: yellow;
    //     transition: 0.5s;
    //     content: url()
    // }

}

blockquote {
    font-family:"aw-conqueror-didot";
    font-weight: 400;
    color: $color-black;
    font-size: 1.75rem;
}

.blackletter {
    font-family: "UnifrakturMaguntia", cursive;
    font-size: 1.2rem; 
}

// page g
body {
    background-color: white;
    margin: 0px;
  }

nav {
    display: none;
}

// hor scroll
// https://redstapler.co/pure-css-horizontal-scrolling-website-tutorial/#:~:text=First%20we%20will%20create%20a,is%20in%20the%20right%20position.
@mixin block_width {
    width: 60%;
}

.e_block {
    @include block_width;
    margin-left: 50px;
    padding-top: $wrapper-padding;
    padding-bottom: $wrapper-padding;
    min-width: 640px;
    max-width: 1000px;
    // height: 70%;
    min-height: 600px;
    // max-height: 725px;
    img {
        width: 100%;
        // height: auto;
    }
  }

.e_subblock {
    padding-left: 25px;
    border-left: 1px solid $color-black;
    max-height: 725px;
}

.wrapper {
    display: flex;
    flex-direction: row;
    width: $view-width*4;
    height: 100vw;
    transform: rotate(90deg) translateY(-100vh);
    transform-origin: top left;
}

.outer-wrapper {
    width: 100vh;
    height: 100vw;
    transform: rotate(-90deg) translateX(-100vh);
    transform-origin: top left;
    overflow-y: scroll;
    overflow-x: hidden;
    position: absolute;
}

::-webkit-scrollbar {
    display:none;
}

// general post css
.e_metadata {
    display: flex;
    flex-direction: row;
    align-items: baseline;
    margin-top: 0px;
    .location {
        margin: 0;
        padding-left: 10px;
        padding-right: 10px;
    }
    .time {
        margin: 0;
    }
    .date {
        margin-right: 1px;
    }
}

.img {
    display: block;
}

.chap_video {
    display: block;
}

// post size classes
.X-Small {
    background-color: $color-white;
    display: flex;
    min-width: 450px;
    max-width: 450px;
    .e_subblock {
        width: 70%;
        margin: 20px;
    }
    .entry_div {
        align-self: flex-start;
    }
    p {
        @include p-serif();
    }
    ul {
        display: none;
    }
}

.Small {
    background-color: $color-white;
    .e_subblock {
        margin-left: 25px;
    }
    .entry_div {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto 45px auto auto;
        grid-column-gap: 25px;
        grid-row-gap: 0px;
        .img_div {
            grid-column: 2;
            grid-row: 1 / 5;
            justify-self: center;
            padding-left: 50px;
            border-left: 1px solid $color-black;
            img {
                width: 278px;
                height: auto;
            }
        }
        .e_title, .e_metadata, .content_div {
            grid-column: 1;
        }
    }
    
}

.Medium {
    // background-color: $color-heavy-metal;
    mix-blend-mode: difference;
    max-width: $medium-entry;
    min-width: $medium-entry; // from Figma entry design
    h2 {
        column-count: 1;
        column-span: 1;
    }
    .entry_div {
        // background-color: red;
        margin-left: 25px;
        .content_div {
            height: 500px;
            column-count: 2;
            column-gap: 25px;
            column-fill: auto;
        }
    }
    p {
        @include p-serif();
    }
}

.Large {
    // background-color: blue;
    max-width: $large-entry;
    min-width: $large-entry; // from Figma entry design
    .entry_div {
        margin-left: 25px;
    }
    .content_div {
        column-count: 3;
        column-gap: 25px;
        column-fill: auto;
    }

}

.X-Large {
    background-color: orange;
}

// post custom classes
.Reflect {
    text-align: right;
    p {
        @include p-serif();
    }
    .e_metadata {
        flex-direction: row-reverse;
        .date {
            margin-left: 5px;
        }
    }
}

