.BigTime {
    padding-left: 265px; // width of BigTime text (approx. 205px)
    .content_div {
        margin-top: 40px;
    }
    .entry_div {
        @include VR_left();  
        .e_metadata {
            margin: 0px;
            height: 25px;
            margin-top: -98px;
            padding-bottom: 100px;
            .time {
                position: relative;
                left: -300px;
                margin-top: -20px;
                span {
                    &::after {
                        content: "";
                        display: block;
                      }
                }
                font-size: 11.25rem;
                line-height: 9rem;
                -webkit-text-fill-color: $color-white; /* Will override color (regardless of order) */
                -webkit-text-stroke-width: 1px;
                -webkit-text-stroke-color: $color-black;
                .time_colon {
                    margin: -2rem;
                    animation: blink 3s step-start infinite;
                }
            }
            .location {
                margin-left: -205px;
                margin-top: -100px;
            }  
        }
    }
}

@keyframes blink {
    20% {
        opacity: 0;
    }
    80% {
        opacity: 1;
    }
}

.AlignEntryCenter {
    // background-color: green;
    max-width: 800px;
    min-width: 800px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    .entry_div {
        display: grid;
        flex-direction: column;
        .e_title {
            order: 1;
            padding-bottom: 50px;
        }
        .e_metadata {
            justify-content: center;
            order: 3;
        }
        .content_div {
            column-count: 1;
            order: 4;
        }
        .img_div {
            order: 2;
            padding-bottom: 10px;
            img {
                width: 316px;
                height: 275px;
                object-fit: cover;
            }
        }
    }

}

.ImgAfter {
    .entry_div {
        display: grid;
        grid-template-columns: $medium-entry 1fr;
        grid-template-rows: auto auto auto auto;
        grid-gap: 25px;
        grid-row-gap: 0px;
        .img_div, .chap_video {
            grid-column: 2;
            grid-row: 1 / 5;
            margin-top: -$wrapper-padding;
            background-color: $color-white-rock;
            img, video {
                height: 100vh;
                width: auto;
                mix-blend-mode: multiply;
            }
        }
        .e_title, .e_metadata, .content_div {
            grid-column: 1;
        }
        .content_div {
            column-count: 2;
        }
    }
}
#entry_sb_12, #entry_sb_12b, #entry_sb_12c {
    background-color: $color-dust-storm;
    padding: 5em;
    position: absolute;
    width: 29em;
}
#entry_sb_12 {
    z-index: 5;
    transform: translate(-50px, 100px) rotate(-112deg);
    transition: 1s ease-in-out 1s;
}
#entry_sb_12:hover {
    transform: translate(50px, 50px) rotate(0deg) scale(1.15);
    box-shadow:  11px 10px 85px -19px rgba(0,0,0,0.25);
    transition: 0.5s ease;
    position: fixed;
}
#entry_sb_12b {
    z-index: 4;
    // transform: (200px, -200px), rotate(200deg);
    transform: translate(-50px, -200px) rotate(112deg);
}
#entry_sb_12c {
    z-index: 3;
    transform: translate(-50px, 500px) rotate(112deg);
}
.Scatter {
    background-color: $color-muted-pink;
    height: 100vh;
    min-width: 360px;
    overflow: hidden;
    position: relative;
    .e_subblock {
        border-left: none;
    }
    .entry_div {
        display: grid;
        grid-template-columns: 1fr;
        .img_div {
            grid-column: 1;
            grid-row: 1 / 1;
        }
    }
}

